.tabs-demo {
	margin: 60px 0;
	ul {
		list-style: none;
		text-align: center; }
	&.left-corner ul li:after {
		transform-origin: 0 100%; }
	&.right-corner ul li:after {
		transform-origin: 100% 100%; }
	li {
		display: inline-block;
		padding: 10px 40px;
		text-transform: uppercase;
		position: relative;
		text-align: center;
		text-indent: 0 !important;
		color: #fff;
		margin: 0 -.3em;
		&::before {
			display: none !important; }
		&::after {
			content: '' !important;
			position: absolute;
			z-index: -1;
			left: 0;
			right: 0;
			top: 0;
			height: 100%;
			width: 100%;
			transform-origin: 50% 100%;
			border-radius: .5em .5em 0 0;
			background: #ccc linear-gradient(#1cefff, #bdc3c7);
			border: 3px solid #888;
			border-bottom-width: 2px;
			transform: scale(1.1, 1.3) perspective(.5em) rotateX(5deg); }
		&.selected {
			z-index: 2;
			&::after {
				border-bottom: none; } } } }
